@use '../../variables' as var;
@use '../../utilityClasses' as utils;
@use '../../mediaQuery' as media;


.searchContainer{
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    justify-content: center;
    background: var.$light-color;
    box-shadow: 0px 3px 4px transparentize(var.$dark-color, 0.8);
    @include media.desktop1000{
        padding: 4em 1em 1.5em;
    }

    

    .serchResult{
        font-size: 2.5rem;
        margin-bottom: 0.25em;
    }

    .serchRelated{
        display: flex;
        gap: 0.5em;
        align-items: flex-start;
        @include media.mobile600{
            flex-direction: column;
            
        }

        p{
            font-weight: 600;
            color: darken(var.$grey-color, 40);
        }
    }

   

    .searchRelated__List{
        display: flex;
        gap: 0.5em;
        flex-wrap: wrap;
        
       
    }

   

    .searchRelated__List a{
        border: 1px solid var.$grey-color;
        padding: 0.25em 1em;
        border-radius: 9999px;
        color: var.$dark-color;
    }
}




